import React, { useState } from 'react';
import "./styles.scss";

export const Textbox = (props) => {
    return (
        <div className="textbox" style={props.style}>
            <input value={props.value} type="textbox" placeholder={props.placeholder} onChange={(e) => props.onChanged(e.target.value)} defaultValue={props.defaultValue}/>
        </div>
    )
}

export const IconTextbox = (props) => {
    const Icon = props.icon;

    return (
        <div className="textbox-icon-textbox" style={props.style}>
            <div className="icon-container" >
                <Icon className="icon"/>
            </div>
            <Textbox placeholder={props.placeholder} onChanged={props.onChanged} defaultValue={props.defaultValue}/>
        </div>
    )
}

export const ColorTextbox = (props) => {
    return (
        <div className="textbox-icon-textbox" style={props.style}>
            <div className="color-container">
                <input type="color" onChange={(e) => {
                    props.onChanged(e.target.value.toUpperCase());
                }} value={props.value}/>
            </div>
            <Textbox onChanged={props.onChanged} value={props.value}/>
        </div>
    )
}

export const Textarea = (props) => {
    return (
        <textarea className="textbox textarea" placeholder={props.placeholder} onChange={(e) => props.onChanged(e.target.value)}>{props.children}</textarea>
    )
}