import React, {useState} from 'react';
import {ContainerCard, CollapseCard} from '../../components/Card';
import {ColorSelectBoxMenu, ImageSelectBoxMenu, ImageSelectDetailBoxMenu} from '../../components/Selection';
import {Textbox, IconTextbox,Textarea, ColorTextbox} from '../../components/Textbox';
import {IoEarthOutline, IoAdd, IoLocationOutline ,IoChevronForwardOutline} from 'react-icons/io5';
import {FiFacebook, FiInstagram, FiTwitter} from 'react-icons/fi';
import {FaXing, FaTelegram, FaTiktok} from 'react-icons/fa';
import {AiOutlineMail, AiOutlineSkype, AiOutlineBehance, AiOutlineYoutube, AiOutlineWhatsApp} from 'react-icons/ai';
import {CircleButtonOutline, PrimaryButton} from '../../components/Button';
import {SocialList} from '../../components/List';
import {CardPreview} from '../../components/CardPreview';
import Axios from 'axios';
import {IMGUR_CLIENT_ID, WEBSITE_URL} from '../../global/variables';
import backgroundImagesvg from './banner.png';
import QRCode from 'qrcode.react';
import "./styles.scss";

var generateID = function () {
    // Math.random should be unique because of its seeding algorithm.
    // Convert it to base 36 (numbers + letters), and grab the first 9 characters
    // after the decimal.
    return Math.random().toString(36).substr(2, 9);
  };

export const CardGenerator = (props) => {
    const themeColors = ["#608FFF", "#374793", "#D51A47", "#FF8F03"];
    const [backgroundImages, setBackgroundImages] = useState(["no-image", "https://www.w3schools.com/css/img_lights.jpg","https://static.vecteezy.com/packs/media/components/global/search-explore-nav/img/vectors/term-bg-1-666de2d941529c25aa511dc18d727160.jpg", "upload-image"]);
    const [profileImages, setProfileImages] = useState(backgroundImages);
    const [imagesGallery, setImagesGallery] = useState(backgroundImages);
    const [loaderImages, setLoaderImages] = useState(backgroundImages);
    const [themeColor, setThemeColor] = useState(0);
    const [primaryColor, setPrimaryColor] = useState(themeColors[0]);
    const [secondaryColor, setSecondaryColor] = useState(themeColors[0]);
    const [backgroundImage, setBackgroundImage] = useState(1);
    const [profileImage, setProfileImage] = useState(1);
    const [loaderImage, setLoaderImage] = useState(1);
    const [firstName, setFirstname] = useState("First name");
    const [lastName, setLastName] = useState("Last name");
    const [mobile, setMobile] = useState("");
    const [work, setWork] = useState("Work");
    const [email, setEmail] = useState("");
    const [companyName, setCompanyName] = useState("Your company name");
    const [jobTitle, setJobTitle] = useState("Your job title");
    const [address, setAddress] = useState("Your address");
    const [summary, setSummary] = useState("A brief description about you and your skills");
    const [qrId, setQrId] = useState(generateID());
    const [website, setWebsite] = useState([{
        name: "",
        value: ""
    }]);
    const [socialMedia, setSocialMedia] = useState([{
        name: "fb",
        active: true,
        value: ""
    },{
        name: "ins",
        active: false,
        value: ""
    },{
        name: "tw",
        active: false,
        value: ""
    },{
        name: "sk",
        active: false,
        value: ""
    },{
        name: "be",
        active: false,
        value: ""
    },{
        name: "xi",
        active: false,
        value: ""
    },{
        name: "yt",
        active: false,
        value: ""
    },{
        name: "ws",
        active: false,
        value: ""
    },{
        name: "wa",
        active: false,
        value: ""
    },{
        name: "lc",
        active: false,
        value: ""
    },{
        name: "ml",
        active: false,
        value: ""
    },{
        name: "te",
        active: false,
        value: ""
    },{
        name: "ti",
        active: false,
        value: ""
    }]);

    const changeEmail = (e) => {
        setQrId(e.split("@")[0]);
        setEmail(e);
    }

    const changeThemeColor = (colorIndex) => {
        setThemeColor(colorIndex);
        setPrimaryColor(themeColors[colorIndex]);
        setSecondaryColor(themeColors[colorIndex]);
    }

    const handleUploadImage = (e, imageIndex) => {
        console.log(e);
        //Upload image with axios
        const file = e.target.files[0];
        const formData = new FormData();
        formData.append('image', file);
        Axios.post('https://api.imgur.com/3/upload', formData, {
            headers: {
                Authorization: `Client-ID ${IMGUR_CLIENT_ID}`,
            }
        })
        .then(res => {
            console.log(res);
        })
    }

    const handleChangeImage = (e, imageIndex) => {
        if(imageIndex == 0) {
            setBackgroundImage(e);
        }else if(imageIndex == 1) {
            setProfileImage(e);
        }else if(imageIndex == 2) {
            setLoaderImage(e);
        }
    }

    const downloadQR = () => {
        const canvas = document.getElementById('qrcode');
        const pngUrl = canvas.toDataURL('image/png').replace('image/png', 'image/octet-stream');
        //console.log('pngUrl', pngUrl);
        let downloadLink = document.createElement('a');
        downloadLink.href = pngUrl;
        downloadLink.download = `${qrId}-qr.png`;
        document.body.appendChild(downloadLink);
        downloadLink.click();
        document.body.removeChild(downloadLink);
    };

    return (
        <>
        <div className="background-image">
            <div className="background-image-img" style={{background: `url(${backgroundImagesvg}) no-repeat center center / cover`}}></div>
            <div className="background-color"></div>
        </div>
        <div className="container">  
            <div className="row">
                <div className="col-md-8">
                    <ContainerCard title="QR Code Generator for vCard Plus">
                        <CollapseCard title="Design & Customize">
                            <div className="row">
                                <div className="col-md-3">
                                    Colors
                                </div>
                                <div className="col-md-9">
                                    <ColorSelectBoxMenu colors={themeColors} selection={themeColor} onChange={changeThemeColor}/>

                                    <div className="row mt-3">
                                        <div className="col-md-6">
                                            <label>Primary</label>
                                            <ColorTextbox placeholder="Color" value={primaryColor} onChanged={setPrimaryColor}/>
                                        </div>
                                        <div className="col-md-6">
                                            <label>Secondary</label>
                                            <ColorTextbox placeholder="Color" value={secondaryColor} onChanged={setSecondaryColor}/>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="row border-top mt-4 pt-3">
                                <div className="col-md-3">
                                    Background Image
                                </div>
                                <div className="col-md-9">
                                    <ImageSelectBoxMenu onUpload={e => handleUploadImage(e, 0)} onChange={(e) => handleChangeImage(e, 0)} selection={backgroundImage} images={backgroundImages} />
                                </div>
                            </div>
                            <div className="row border-top mt-4 pt-3">
                                <div className="col-md-3">
                                    Profile Image
                                </div>
                                <div className="col-md-9">
                                    <ImageSelectBoxMenu onUpload={e => handleUploadImage(e, 1)} onChange={(e) => handleChangeImage(e, 1)} selection={profileImage} images={profileImages}/>
                                </div>
                            </div>

                        </CollapseCard>
                        <CollapseCard title="Basic Information">
                            <div className="row">
                                <div className="col-md-3">
                                    Name
                                </div>
                                <div className="col-md-9">
                                    <div className="row">
                                        <div className="col-md-6">
                                            <Textbox placeholder="First name" onChanged={(e) => setFirstname(e)}/>
                                        </div>
                                        <div className="col-md-6">
                                            <Textbox placeholder="Last name" onChanged={(e) => setLastName(e)}/>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-3">
                                    Numbers
                                </div>
                                <div className="col-md-9">
                                    <div className="row">
                                        <div className="col-md-6">
                                            <Textbox placeholder="Mobile" onChanged={(e) => setMobile(e)}/>
                                        </div>
                                        <div className="col-md-6">
                                            <Textbox placeholder="Work" onChanged={(e) => setWork(e)}/>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-3">
                                    Email
                                </div>
                                <div className="col-md-9">
                                    <Textbox placeholder="Your email id" onChanged={changeEmail}/>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-3">
                                    Company
                                </div>
                                <div className="col-md-9">
                                    <div className="row">
                                        <div className="col-md-6">
                                            <Textbox placeholder="Your company name" onChanged={(e) => setCompanyName(e)}/>
                                        </div>
                                        <div className="col-md-6">
                                            <Textbox placeholder="Your job title" onChanged={(e) => setJobTitle(e)}/>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-3">
                                    Address
                                </div>
                                <div className="col-md-9">
                                    <Textarea placeholder="Your address" onChanged={(e) => setAddress(e)}></Textarea>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-3">
                                    Summary
                                </div>
                                <div className="col-md-9">
                                    <Textarea placeholder="A brief description about you and your skills" onChanged={(e) => setSummary(e)}></Textarea>
                                </div>
                            </div>
                        </CollapseCard>
                        <CollapseCard title="Web Links">
                            {website.map((item, i) => {
                                return(
                                    <div className="row" key={`web-url-input-${i}`}>
                                        <div className="col-md-3">

                                        </div>
                                        <div className="col-md-9">
                                            <div className="row">
                                                <div className="col-md-3">
                                                    <Textbox placeholder="My Website" onChanged={(e) => {
                                                        let websites = [...website];
                                                        websites[i].name = e;
                                                        setWebsite(websites);
                                                    }}/>
                                                </div>
                                                <div className="col-md-9">
                                                    <IconTextbox icon={IoEarthOutline} placeholder="www.yourweburl.com" onChanged={(e) => {
                                                        let websites = [...website];
                                                        websites[i].value = e;
                                                        setWebsite(websites);
                                                    }}/>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                )
                            })}

                            <div className="row" onClick={() => {
                                setWebsite([...website, {url: '', name: ''}])
                            }}>
                                <CircleButtonOutline icon={IoAdd} style={{marginLeft: "auto", marginRight: "1rem"}}/>
                            </div>
                        </CollapseCard>
                        <CollapseCard title="Images Upload">
                            <div className="row">
                                <div className="col-md-3">
                                    Images
                                </div>
                                <div className="col-md-9">
                                <ImageSelectDetailBoxMenu  selection={1} images={[
                                    {
                                        title: "Title 1",
                                        image: "https://www.w3schools.com/css/img_lights.jpg"
                                    },
                                    {
                                        title: "Title 2",
                                        image: "https://static.vecteezy.com/packs/media/components/global/search-explore-nav/img/vectors/term-bg-1-666de2d941529c25aa511dc18d727160.jpg"
                                    }, 
                                    {
                                        image: "upload-image"
                                    }
                                ]}/>
                                </div>
                            </div>
                        </CollapseCard>
                        <CollapseCard title="Social Media">
                            <div className="row">
                                <div className="col-md-3">

                                </div>
                                <div className="col-md-9">
                                    {socialMedia.map((item, index) => {
                                        if(item.name == 'fb' && item.active) return <IconTextbox icon={FiFacebook} placeholder="www.facebook.com/yourpage" onChanged={(e) => {
                                            let socialMedias = socialMedia;
                                            socialMedias[index].value = e;
                                            setSocialMedia(socialMedias);
                                        }}/>
                                        if(item.name == 'ins' && item.active) return <IconTextbox icon={FiInstagram} placeholder="www.instagram.com/yourpage" onChanged={(e) => {
                                            let socialMedias = [...socialMedia];
                                            socialMedias[index].value = e;
                                            setSocialMedia(socialMedias);
                                        }}/>
                                        if(item.name == 'tw' && item.active) return <IconTextbox icon={FiTwitter} placeholder="www.twitter.com/yourpage" onChanged={(e) => {
                                            let socialMedias = [...socialMedia];
                                            socialMedias[index].value = e;
                                            setSocialMedia(socialMedias);
                                        }}/>
                                        if(item.name == 'sk' && item.active) return <IconTextbox icon={AiOutlineSkype} placeholder="Skype id" onChanged={(e) => {
                                            let socialMedias = [...socialMedia];
                                            socialMedias[index].value = e;
                                            setSocialMedia(socialMedias);
                                        }}/>
                                        if(item.name == 'be' && item.active) return <IconTextbox icon={AiOutlineBehance} placeholder="www.behance.com/yourpage" onChanged={(e) => {
                                            let socialMedias = [...socialMedia];
                                            socialMedias[index].value = e;
                                            setSocialMedia(socialMedias);
                                        }}/>
                                        if(item.name == 'xi' && item.active) return <IconTextbox icon={FaXing} placeholder="www.xing.com/yourpage" onChanged={(e) => {
                                            let socialMedias = [...socialMedia];
                                            socialMedias[index].value = e;
                                            setSocialMedia(socialMedias);
                                        }}/>
                                        if(item.name == 'yt' && item.active) return <IconTextbox icon={AiOutlineYoutube} placeholder="www.youtube.com/yourpage" onChanged={(e) => {
                                            let socialMedias = [...socialMedia];
                                            socialMedias[index].value = e;
                                            setSocialMedia(socialMedias);
                                        }}/>
                                        if(item.name == 'ws' && item.active) return <IconTextbox icon={IoEarthOutline} placeholder="https://qrcodechimp.com" onChanged={(e) => {
                                            let socialMedias = [...socialMedia];
                                            socialMedias[index].value = e;
                                            setSocialMedia(socialMedias);
                                        }}/>
                                        if(item.name == 'wa' && item.active) return <IconTextbox icon={AiOutlineWhatsApp} placeholder="https://wa.me/phonenumber" onChanged={(e) => {
                                            let socialMedias = [...socialMedia];
                                            socialMedias[index].value = e;
                                            setSocialMedia(socialMedias);
                                        }}/>
                                        if(item.name == 'lc' && item.active) return <IconTextbox icon={IoLocationOutline} placeholder="123, Street, City, State, Zipcode" onChanged={(e) => {
                                            let socialMedias = [...socialMedia];
                                            socialMedias[index].value = e;
                                            setSocialMedia(socialMedias);
                                        }}/>
                                        if(item.name == 'ml' && item.active) return <IconTextbox icon={AiOutlineMail} placeholder="abc@domain.com" onChanged={(e) => {
                                            let socialMedias = [...socialMedia];
                                            socialMedias[index].value = e;
                                            setSocialMedia(socialMedias);
                                        }}/>
                                        if(item.name == 'te' && item.active) return <IconTextbox icon={FaTelegram} placeholder="Telegram id" onChanged={(e) => {
                                            let socialMedias = [...socialMedia];
                                            socialMedias[index].value = e;
                                            setSocialMedia(socialMedias);
                                        }}/>
                                        if(item.name == 'ti' && item.active) return <IconTextbox icon={FaTiktok} placeholder="https://vt.tiktok.com/profile-link" onChanged={(e) => {
                                            let socialMedias = [...socialMedia];
                                            socialMedias[index].value = e;
                                            setSocialMedia(socialMedias);
                                        }}/>
                                    })}
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-3">

                                </div>
                                <div className="col-md-9">
                                    <SocialList data={socialMedia} onChange={e => {
                                        let socialMd = [...socialMedia];
                                        socialMd[e].active = !socialMd[e].active;
                                        setSocialMedia(socialMd);
                                    }}/>
                                </div>
                            </div>
                        </CollapseCard>
                        <CollapseCard title="Page Loader">
                            <div className="row">
                                <div className="col-md-3">
                                    Loader image
                                </div>
                                <div className="col-md-6">
                                    <ImageSelectBoxMenu  onUpload={e => handleUploadImage(e, 2)} onChange={(e) => handleChangeImage(e, 2)} selection={loaderImage} images={loaderImages}/>
                                </div>
                            </div>
                        </CollapseCard>
                    </ContainerCard>
                    <PrimaryButton onClick={downloadQR}>
                        Save QR Code
                        <IoChevronForwardOutline/>
                    </PrimaryButton>
                </div>
                <div className="col-md-4" style={{paddingLeft: 0}}>
                    <ContainerCard title="Preview">
                        <CardPreview data={{
                            primaryColor,
                            secondaryColor,
                            backgroundImages,
                            backgroundImage,
                            profileImages,
                            profileImage,
                            themeColors,
                            themeColor,
                            firstName,
                            lastName,
                            mobile,
                            work,
                            email,
                            companyName,
                            jobTitle,
                            address,
                            summary,
                            website,
                            imagesGallery,
                            socialMedia,
                            loaderImages,
                            loaderImage,
                    }}/>
                    </ContainerCard>

                    <CollapseCard title="QR Code Design">
                        <div style={{display: 'flex', alignItems: 'center', justifyContent: 'center'}}>
                            <QRCode
                              id='qrcode'
                              value={`${WEBSITE_URL}/profile/${qrId}`}
                              size={290}
                              level={'H'}
                              includeMargin={true}
                            />
                        </div>
                    </CollapseCard>
                </div>
            </div>
        </div>
        </>
    )
}