import React from 'react';
import "./styles.scss";

export const CircleButtonOutline = (props) => {
    const Icon = props.icon;

    return(
        <div className="button circle_button_outline" style={props.style}>
            <Icon className="icon"/>
        </div>
    )
}

export const PrimaryButton = (props) => {
    return (
        <div className="button primary" onClick={props.onClick}>
            {props.children}
        </div>
    )
}