import React  from 'react';
import {BsCheck, BsArrowUp} from 'react-icons/bs';
import {IoCloseOutline, IoCloudUploadOutline} from 'react-icons/io5';

import "./styles.scss";

export const ColorSelectBox = (props) => {
    return(
        <div className={`selection color-box ${props.active ? 'active' : ''}`} onClick={props.onClick}>
            <div className="color-container">
                <div className="checkbox">
                    <BsCheck className="icon"/>
                </div>
                <div className="color-1" style={{background: props.color}}>

                </div>
                <div className="color-2" style={{background: props.color}}>

                </div>
            </div>
        </div>
    )
}

export const ColorSelectBoxMenu = (props) => {
    return (
        <div className="selection color-menu">
            {props.colors.map((color, index) => {
                return (
                    <ColorSelectBox active={props.selection == index} key={`color-box-key-${index}`} index={index} color={color} onClick={() => props.onChange(index)}/>
                )
            })}
        </div>
    )
}

//*IMAGE SELECTION
export const ImageSelectBox = (props) => {
    return (
        <div className={`selection image-box ${props.active ? 'active' : ''}`} style={props.style} onClick={props.image == 'upload-image' ? null : props.onClick}>
            <div className="image-container">
                <div className="checkbox">
                    <BsCheck className="icon"/>
                </div>
                <div className={`image ${props.image == 'upload-image' ? 'action' : ''}`} style={{backgroundImage: `url(${props.image})`}}>
                    <IoCloseOutline className={`icon ${props.image !== 'no-image' ? 'hide' : ''}`}/>
                    <IoCloudUploadOutline className={`icon ${props.image !== 'upload-image' ? 'hide' : ''}`}/>
                    
                    {props.image == 'upload-image' ? 
                        <>
                            <input type="file" id="file" onChange={props.onUpload}/>
                            <label for="actual-btn" htmlFor='file'></label>
                        </>   : ''  
                    }
                </div>
            </div>
        </div>
    )
}

export const ImageSelectBoxMenu = (props) => {
    return (
        <div className="selection color-menu">
            {props.images.map((image, index) => {
                return (
                    <ImageSelectBox active={props.selection == index} key={`image-box-key-${index}`} image={image} onUpload={props.onUpload} onClick={() => props.onChange(index)}/>
                )
            })}
        </div>
    )
}

export const ImageSelectDetailBox = (props) => {
    return(
        <div className={`selection image-detail-box ${props.active ? 'active' : ''}`}>
            <div className="image" style={{backgroundImage: `url(${props.data.image})`}}></div>
            <div className="detail">
                <span className="name">{props.data.title}</span>
            </div>
            <div className="control">
                <div className="action-btn">
                    <BsArrowUp className="icon"/>
                </div>
                <div className="action-btn">
                    <IoCloseOutline className="icon"/>
                </div>
            </div>
        </div>
    )
}

export const ImageSelectDetailBoxMenu = (props) => {
    return(
        <div className="selection detail-menu">
            {props.images.map((data, index) => {
                if(data.image == 'upload-image') {
                    return (
                        <ImageSelectBox image="upload-image" style={{marginTop: 16}}/>
                    )
                }

                return (
                    <ImageSelectDetailBox active={props.selection == index} key={`image-box-key-${index}`} data={data} onChange={props.onChange}/>
                )
            })}
        </div>
    )
}