import React from 'react';
import {FiFacebook, FiInstagram, FiTwitter} from 'react-icons/fi';
import {AiOutlineMail, AiOutlineSkype, AiOutlineBehance, AiOutlineYoutube, AiOutlineWhatsApp} from 'react-icons/ai';
import {FaXing, FaTelegram, FaTiktok} from 'react-icons/fa';
import {IoEarthOutline, IoLocationOutline} from 'react-icons/io5';
import "./styles.scss";

export const SocialList = (props) => {
    return(
        <ul className="list social">
            <li className={props.data[0].active ? 'active' : ''} onClick={() => props.onChange(0)}>
                <FiFacebook className="icon"/>
            </li>
            <li className={props.data[1].active ? 'active' : ''} onClick={() => props.onChange(1)}>
                <FiInstagram className="icon"/>
            </li>
            <li className={props.data[2].active ? 'active' : ''} onClick={() => props.onChange(2)}>
                <FiTwitter className="icon"/>
            </li>
            <li className={props.data[3].active ? 'active' : ''} onClick={() => props.onChange(3)}>
                <AiOutlineSkype className="icon"/>
            </li>
            <li className={props.data[4].active ? 'active' : ''} onClick={() => props.onChange(4)}>
                <AiOutlineBehance className="icon"/>
            </li>
            <li className={props.data[5].active ? 'active' : ''} onClick={() => props.onChange(5)}>
                <FaXing className="icon"/>
            </li>
            <li className={props.data[6].active ? 'active' : ''} onClick={() => props.onChange(6)}>
                <AiOutlineYoutube className="icon"/>
            </li>
            <li className={props.data[7].active ? 'active' : ''} onClick={() => props.onChange(7)}>
                <IoEarthOutline className="icon"/>
            </li>
            <li className={props.data[8].active ? 'active' : ''} onClick={() => props.onChange(8)}>
                <AiOutlineWhatsApp className="icon"/>
            </li>
            <li className={props.data[9].active ? 'active' : ''} onClick={() => props.onChange(9)}>
                <IoLocationOutline className="icon"/>
            </li>
            <li className={props.data[10].active ? 'active' : ''} onClick={() => props.onChange(10)}>
                <AiOutlineMail className="icon"/>
            </li>
            <li className={props.data[11].active ? 'active' : ''} onClick={() => props.onChange(11)}>
                <FaTelegram className="icon"/>
            </li>
            <li className={props.data[12].active ? 'active' : ''} onClick={() => props.onChange(12)}>
                <FaTiktok className="icon"/>
            </li>
        </ul>
    )
}