import React, {useState} from 'react';
import './styles.scss';
import {AiOutlineMinus} from 'react-icons/ai';
import {VscAdd} from 'react-icons/vsc';

export const ContainerCard = (props) => {
    return (
        <div className="card container-c" style={props.style}>
            <div className="header">
                <h3 className="title">{props.title}</h3>
                <div className="control-container">

                </div>
            </div>
            <div className="container">
                {props.children}
            </div>
        </div>
    )
}

export const CollapseCard = (props) => {
    const [hide, setHide] = useState(false);

    return(
        <div className={`card collapse-c mb-3 ${hide ? 'hide' : ''}`} style={props.style}>
            <div className="header" onClick={() => setHide(!hide)}>
                <h3 className="title">{props.title}</h3>
                <div className="control-container">
                    <div className="control">
                        {hide ? <VscAdd className="icon"/> : <AiOutlineMinus className="icon"/>}
                    </div>
                </div>
            </div>
            <div className="container">
                {props.children}
            </div>
        </div>
    )
}