import React from 'react';
import {FiPhoneCall, FiFacebook, FiTwitter, FiLinkedin} from 'react-icons/fi';
import {IoPersonAddOutline, IoInformation, IoLocationOutline, IoEarthOutline} from 'react-icons/io5';
import {FiInstagram} from 'react-icons/fi';
import {AiOutlineMail, AiOutlineSkype, AiOutlineBehance, AiOutlineYoutube, AiOutlineWhatsApp} from 'react-icons/ai';
import {FaXing, FaTelegram, FaTiktok, FaSkype} from 'react-icons/fa';
import "./styles.scss";

export const CardPreview = (props) => {
    const data = props.data;
    
    return (
        <div className="card-preview">
            <div className="background" style={{background: `url(${data.backgroundImages[data.backgroundImage]}) no-repeat center center / cover`}}></div>
            <div className="information-container" style={{backgroundColor: data.primaryColor}}>
                <div className="first-information">
                    <div className="avatar-container">
                        <div className="avatar" style={{background: `url(${data.profileImages[data.profileImage]}) no-repeat center center / cover`}}></div>
                    </div>
                    <span className="user-name">{data.firstName} {data.lastName}</span>
                    <span className="job">{data.jobTitle}</span>
                </div>
                <div className="information-group">
                    <div className="information-gr-header">
                        <div className="left">
                            <FiPhoneCall className="icon"/>
                            <span className="title">Contact</span>
                        </div>
                        <div className="right">
                            <div className="action-btn">
                                <span>Save To Contacts</span>
                                <div className="circle-container">
                                    <IoPersonAddOutline className="icon"/>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="information-input">
                        <span className="label">Name</span>
                        <span className="value">{data.firstName} {data.lastName}</span>
                    </div>
                    <div className="information-input">
                        <span className="label">Work</span>
                        <span className="value">{data.work}</span>
                    </div>
                    <div className="information-input">
                        <span className="label">Company</span>
                        <span className="value">{data.companyName}</span>
                    </div>
                </div>
                <div className="information-group">
                    <div className="information-gr-header">
                        <div className="left">
                            <IoInformation className="icon"/> 
                            <span className="title">Summary</span>
                        </div>
                    </div>
                    <span className="content">{data.summary}</span>
                </div>
                <div className="information-group">
                    <div className="information-gr-header">
                        <div className="left">
                            <IoLocationOutline className="icon"/> 
                            <span className="title">Location</span>
                        </div>
                    </div>
                    <span className="content">{data.address}</span>
                    <div className="action-button-2">
                        <span>Show on Map</span>
                    </div>
                </div>
                {data.website[0].value == "" && data.website[0].name == "" ? null : 
                    <div className="information-group">
                        <div className="information-gr-header">
                            <div className="left">
                                <IoEarthOutline className="icon"/> 
                                <span className="title">Web Links</span>
                            </div>
                        </div>
                        <span className="content">
                            {data.website.map((item, i) => {
                                return (
                                    <a target="_blank" href={item.value} key={`${i}-website-k`}>{item.name == "" ? "My Website" : item.name}</a>
                                )
                            })}
                        </span>
                    </div>
                }

                <div className="information-group" style={{padding: 0, background: 'none', border: 'none', shadow: 'none'}}>
                    {data.imagesGallery.map((item, i) => {
                        return <img class="information-image" src={`${item}`} alt="" key={`${i}-image-k`}/>
                    })}
                </div>

                <div className="information-group">
                    {data.socialMedia.map((item, i) => {
                        if(item.name == 'fb' && item.active) return <a href={item.value} target="_blank" className="social-group">
                            <div className="circle-container">
                                <FiFacebook className="icon"/>
                            </div>
                            <span className="name">Facebook</span>
                        </a>
                        if(item.name == 'ins' && item.active) return <a href={item.value} target="_blank" className="social-group">
                            <div className="circle-container">
                                <FiInstagram className="icon"/>
                            </div>
                            <span className="name">Instagram</span>
                        </a>
                        if(item.name == 'tw' && item.active) return <a href={item.value} target="_blank" className="social-group">
                            <div className="circle-container">
                                <FiTwitter className="icon"/>
                            </div>
                            <span className="name">Twitter</span>
                        </a>
                        if(item.name == 'sk' && item.active) return <a href={item.value} target="_blank" className="social-group">
                            <div className="circle-container">
                                <AiOutlineSkype className="icon"/>
                            </div>
                            <span className="name">Skype</span>
                        </a>
                        if(item.name == 'be' && item.active) return <a href={item.value} target="_blank" className="social-group">
                            <div className="circle-container">
                                <AiOutlineBehance className="icon"/>
                            </div>
                            <span className="name">Behance</span>
                        </a>
                        if(item.name == 'xi' && item.active) return <a href={item.value} target="_blank" className="social-group">
                            <div className="circle-container">
                                <FaXing className="icon"/>
                            </div>
                            <span className="name">Xing</span>
                        </a>
                        if(item.name == 'yt' && item.active) return <a href={item.value} target="_blank" className="social-group">
                            <div className="circle-container">
                                <AiOutlineYoutube className="icon"/>
                            </div>
                            <span className="name">Youtube</span>
                        </a>
                        if(item.name == 'ws' && item.active) return <a href={item.value} target="_blank" className="social-group">
                            <div className="circle-container">
                                <IoEarthOutline className="icon"/>
                            </div>
                            <span className="name">Website</span>
                        </a>
                        if(item.name == 'wa' && item.active) return <a href={item.value} target="_blank" className="social-group">
                            <div className="circle-container">
                                <AiOutlineWhatsApp className="icon"/>
                            </div>
                            <span className="name">Whats App</span>
                        </a>
                        if(item.name == 'lc' && item.active) return <a href={item.value} target="_blank" className="social-group">
                            <div className="circle-container">
                                <IoLocationOutline className="icon"/>
                            </div>
                            <span className="name">Address</span>
                        </a>
                        if(item.name == 'ml' && item.active) return <a href={item.value} target="_blank" className="social-group">
                            <div className="circle-container">
                                <AiOutlineMail className="icon"/>
                            </div>
                            <span className="name">Mail</span>
                        </a>
                        if(item.name == 'te' && item.active) return <a href={item.value} target="_blank" className="social-group">
                            <div className="circle-container">
                                <FaTelegram className="icon"/>
                            </div>
                            <span className="name">Telegram</span>
                        </a>
                        if(item.name == 'ti' && item.active) return <a href={item.value} target="_blank" className="social-group">
                            <div className="circle-container">
                                <FaTiktok className="icon"/>
                            </div>
                            <span className="name">Tiktok</span>
                        </a>
                    })}
                </div>
            </div>
        </div>
    )
}